/**
 * Simplified Field Components (Mock/Development Version)
 * 
 * These are mock implementations of the simplified field components
 * for use during development and design. In production, these are
 * replaced with the real implementations from packages/ui that include
 * collaboration, type inference, and expression support.
 * 
 * ## API
 * 
 * The API matches the production components exactly, so developers
 * can build and test their UIs without needing the full infrastructure.
 * 
 * ## Example
 * 
 * ```tsx
 * import { Input, Select } from '@process.co/ui';
 * 
 * function CustomUserForm({ value, onChange }) {
 *   return (
 *     <div>
 *       <Input 
 *         fieldName="firstName" 
 *         label="First Name" 
 *         value={value.firstName}
 *         onChange={(v) => onChange({ ...value, firstName: v })}
 *       />
 *       <Select
 *         fieldName="role"
 *         label="Role"
 *         value={value.role}
 *         onChange={(v) => onChange({ ...value, role: v })}
 *         options={['admin', 'user', 'guest']}
 *       />
 *     </div>
 *   );
 * }
 * ```
 */

// ============================================================================
// Field Components
// ============================================================================

export { Input } from './Input.js';
export type { InputProps } from './Input.js';

export { Select } from './Select.js';
export type { SelectProps, SelectOption, SelectRenderProps } from './Select.js';

// ============================================================================
// Mock Context (no-op implementations)
// ============================================================================

/**
 * Mock context value (always returns defaults).
 * In production, this is replaced with the real context from packages/ui.
 */
export interface TemplateFieldContextValue {
  yDoc: null;
  collabUser: null;
  awareness: null;
  availableNodes: any;
  myInterface: any;
  typeDeclarations: string;
  element: any;
  nodeId: string;
  onControlFocus: (context: any) => void;
  onControlBlur: () => void;
  onRecordChange: (change: any) => void;
  onValidationChange: (fieldName: string, error: any) => void;
  parentFieldPath: string | null;
  disabled: boolean;
}

/**
 * Mock hook - returns empty context values.
 * Components work as basic inputs in development mode.
 */
export function useTemplateFieldContext(): TemplateFieldContextValue {
  return {
    yDoc: null,
    collabUser: null,
    awareness: null,
    availableNodes: null,
    myInterface: null,
    typeDeclarations: '',
    element: null,
    nodeId: '',
    onControlFocus: () => {},
    onControlBlur: () => {},
    onRecordChange: () => {},
    onValidationChange: () => {},
    parentFieldPath: null,
    disabled: false,
  };
}

/**
 * Mock hook - always returns false in development mode.
 */
export function useIsInTemplateFieldProvider(): boolean {
  return false;
}

/**
 * Mock hook - returns the fieldName as-is (no parent path in dev mode).
 */
export function useFieldPath(fieldName: string): string {
  return fieldName;
}

/**
 * Mock provider - just renders children without context.
 */
export function TemplateFieldProvider({ children }: { children: React.ReactNode }) {
  return <>{children}</>;
}

/**
 * Mock provider - just renders children without nesting context.
 */
export function NestedFieldProvider({ 
  children 
}: { 
  children: React.ReactNode;
  fieldName: string;
}) {
  return <>{children}</>;
}

// ============================================================================
// Type exports for compatibility
// ============================================================================

export type TemplateFieldProviderProps = {
  children: React.ReactNode;
  nodeId?: string;
  yDoc?: any;
  collabUser?: any;
  awareness?: any;
  availableNodes?: any;
  myInterface?: any;
  typeDeclarations?: string;
  element?: any;
  onControlFocus?: (context: any) => void;
  onControlBlur?: () => void;
  onRecordChange?: (change: any) => void;
  onValidationChange?: (fieldName: string, error: any) => void;
  parentFieldPath?: string | null;
  disabled?: boolean;
};

export type NestedFieldProviderProps = {
  children: React.ReactNode;
  fieldName: string;
};

export type TemplateFieldValidationError = {
  hasError: boolean;
  hasSyntaxError: boolean;
  hasTypeMismatch: boolean;
  expectedType?: string;
  inferredType?: string;
  errorMessage?: string;
  willCoerce?: boolean;
};

export type TemplateFieldFocusContext = {
  nodeId: string;
  fieldName: string;
  propertyPath: string;
  label: string;
  expectedType: string;
  currentValue: any;
  isExpression: boolean;
  editorFormat: string;
  inferredType?: string;
  errors?: any;
  selectedNodeId?: string;
  selectedNodeName?: string;
  elementContext?: any;
  userContext?: any;
  propertyDefinition?: any;
};

export type TemplateFieldChangeEvent = {
  type: 'value_change' | 'mode_switch' | 'format_change' | 'field_focus' | 'panel_open' | 'panel_close';
  details: {
    oldValue?: string;
    newValue?: string;
    oldMode?: string;
    newMode?: string;
    description?: string;
    hasError?: boolean;
    hasTypeMismatch?: boolean;
    willCoerce?: boolean;
    expectedType?: string;
    inferredType?: string;
  };
};

